@import url(https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

.brand-logo {
  float: left;
  font-family: "Grand Hotel", cursive;
}
a {
  color: black !important;
}
.nav-wrapper {
  background-color: white;
}

.logout_button {
  margin-right: auto !important;
}
@media only screen and (max-width: 900px) {
  .nav-wrapper .brand-logo {
    position: absolute !important;
    left: 10% !important;
  }
}
@media only screen and (max-width: 900px) {
  .nav-wrapper .brand-logo {
    position: absolute !important;
    left: 10% !important;
  }
}
@media only screen and (max-width: 500px) {
  .nav-wrapper .brand-logo {
    font-size: 24px;
    margin-right: 5px;
  }
  .nav-wrapper > #nav-mobile {
    width: 400px !important;
  }
}

.profile__and__gallary {
  max-width: 600px;
  margin: 0px auto;
}
.profile {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 18px 0px;
  border-bottom: 1px solid gray;
}
.profile__img {
  width: 160px;
  height: 160px;
  border-radius: 50%;
}
.profile__alldetails {
  display: flex;
  flex-direction: column;
}
.profile__details {
  display: flex;
  justify-content: space-around;
}
.profile__details > h6 {
  margin-right: 10px;
}
.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.item {
  width: 30%;
  margin: 10px;
}
.update_button {
  width: 150px;
  margin: 20px 0px;
}

h2 {
  font-family: "Grand Hotel", cursive;
}
#toast-container {
  top: 40px !important;
  right: 20px !important;
  left: auto !important;
}
.input-field input[type="text"] [type="password"]:focus {
  border-bottom: 1px solid rgb(8, 93, 252) !important;
  box-shadow: 0 1px 0 0 rgb(8, 93, 252) !important;
}

h2 {
  font-family: "Grand Hotel", cursive;
}
.mycard {
  margin-top: 30px;
}
.auth-card {
  padding: 20px;
  text-align: center;
  max-width: 400px;
  margin: 10px auto;
}
.input-field input[type="text"]:focus {
  border-bottom: 1px solid rgb(8, 93, 252) !important;
  box-shadow: 0 1px 0 0 rgb(8, 93, 252) !important;
}

button {
  height: 40px;
  width: 110px;
}

.create__post {
  margin: 30px auto;
  max-width: 600px;
  padding: 20px;
  text-align: center;
}

.home__card {
  max-width: 600px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  margin: 25px auto;
  border-radius: 20px;
}
.material-icons {
  color: gray;
}
.post__image {
  width: 600px;
}
.postedBy {
  font-weight: bolder;
  margin-right: 10px;
}
.delete__icon {
  float: right;
  font-size: 28px;
}
.postedBy__image {
  display: flex;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  margin-right: 10px;
}
.postedBy__name {
  display: flex;
  font-weight: bolder;
  font-size: 26px;
  text-transform: capitalize;
  font-style: italic;
  color: green;
}
.postedBy__namewithimage {
  display: flex;
  align-items: center;
  padding: 10px;
}
.name__deleteIcon {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

